import React, { useImperativeHandle } from "react";
import { Link, withRouter } from "react-router-dom";

import { isLogged, getStorageItem, removeStorageItem, setStorageItem, shop } from "../config/config";
import Api from "../config/Api";

import Header from "../components/Header";
import Footer from "../components/Footer";
import Popup from "../components/Popup";

import Order from "../components/Order";

import "../styles/admin.css";

class AdminShop extends React.Component {

    state = {
        filters: {
            filters: {
                status: "paid"
            },
            limit: 8
        },

        orders: []
    }

    constructor() {
        super();

        this.loadData = this.loadData.bind(this);
        this.fulfillOrder = this.fulfillOrder.bind(this);
        this.changeCategory = this.changeCategory.bind(this);
    }

    async loadData() {
        const token = getStorageItem("token")

        const orders = await Api.getOrders(this.state.filters, token);

        if (orders.message === "Orders retrieved successfully") {
            this.setState({ orders: orders.orders });
        }
    }

    async fulfillOrder(id) {
        const token = getStorageItem("token");

        const fulfill = await Api.fulfillOrder(id, token);

        this.setState({ orders: [] }, () => this.loadData())
    }

    changeCategory(category) {
        this.setState({
            filters: {
                filters: {
                    status: category
                },
                limit: 8
            },
            orders: []
        }, () => {
            this.loadData();
        })
    }

    componentDidMount() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        window.addEventListener("resize", this.updateOffset.bind(this));

        if (!isLogged()) {
            this.props.history.push("/prihlasenie")
        }

        this.loadData();
    }

    componentDidUpdate() {
        if (!isLogged()) {
            this.props.history.push("/prihlasenie")
        }
    }

    componentWillUnmount() {
        window.removeEventListener("resize", this.updateOffset.bind(this));
    }

    updateOffset() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        this.forceUpdate();
    }

    render() {
        return(
            <div className="screen" id="admin-orders">
                <Header />

                {this.state.popup ? (
                    <Popup
                        type="info"
                        title={this.state.title}
                        onClick={() => {
                            this.setState({ popup: false })
                            this.props.history.push("/profil")
                        }}
                        loading={this.state.loading}
                    />
                ) : null}

                <div className="content" style={{ paddingTop: this.state.offset + 50 }}>
                    <div className="title">Administrácia objednávok</div>
                    <div className="text">
                        Tu sú detaily o Vašom účte. V prípade zmeny fakturačných údajov pri objednávke si ich môžete zmeniť upravením údajov uvedených nižšie. 
                    </div>

                    <div className="menu">
                        <div className="item" style={this.state.filters.filters.status === "pending" ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.changeCategory("pending")}>Čaká sa na zaplatenie</div>
                        <div className="item" style={this.state.filters.filters.status === "paid" ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.changeCategory("paid")}>Zaplatené</div>
                        <div className="item" style={this.state.filters.filters.status === "fulfilled" ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.changeCategory("fulfilled")}>Vybavené</div>
                    </div>

                    <div className="orders">
                        {this.state.orders.map((order) => <Order order={order} fulfill={this.fulfillOrder} />)}
                    </div>
                </div>

                <Footer />
            </div>
        )
    }
}

export default withRouter(AdminShop);