import React from "react";
import { Link } from "react-router-dom";

import { blog } from "../config/config";

import Header from "../components/Header";
import Footer from "../components/Footer";
import Title from "../components/Title";

import "../styles/blog.css";

export default class Blog extends React.Component {

    state = {
        offset: 0,

        blog: null
    }

    constructor() {
        super();
    }

    componentDidMount() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        window.addEventListener('resize', this.updateOffset.bind(this));

        this.setState({ blog: blog });
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateOffset.bind(this));
    }

    updateOffset() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        this.forceUpdate();
    }

    render() {
        const width = window.innerWidth;
        const offset = (width > 1100 ? 75 : 50);

        const blog = this.state.blog ? this.state.blog : [];

        return(
            <div className="screen" id="blog">
                <Header />

                <Title title="Blog TerraMia" image="image-blog.jpeg" style={{ marginTop: this.state.offset }} />

                <div className="content">
                    <div className="article-panel">
                        {blog.map((article) => <Article article={article} />)}
                    </div>
                </div>

                <Footer />
            </div>
        )
    }
}

function Article(props) {
    return(
        <Link className="article" to={"/blog/" + props.article.id}>
            <img className="image" src={props.article.image} />
            <div className="date">{props.article.date}</div>
            <div className="title">{props.article.title}</div>
            <div className="text">{props.article.text}</div>
        </Link>
    )
}