import React from "react";
import { Link, withRouter } from "react-router-dom";

import { addToCart, API_URL, shop, blog } from "../config/config";
import Api from "../config/Api";

import Header from "../components/Header";
import Footer from "../components/Footer";
import Loading from "../components/Loading";
import Categories from "../components/Categories";

import "../styles/article.css";

class Article extends React.Component {

    state = {
        offset: 0,

        article: {}
    }

    constructor() {
        super();

        this.loadArticle = this.loadArticle.bind(this);
    }

    loadArticle() {
        const id = parseInt(this.props.match.params.articleId);

        for (let i = 0; i < blog.length; i++) {
            if (blog[i].id === id) {
                this.setState({ article: blog[i] });
                return
            }
        }
    }

    componentDidMount() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        window.addEventListener('resize', this.updateOffset.bind(this));

        this.loadArticle()
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateOffset.bind(this));
    }

    updateOffset() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        this.forceUpdate();
    }

    render() {
        const article = this.state.article

        return(
            <div className="screen" id="article">
                <Header />

                <div className="content" style={{ paddingTop: this.state.offset }}>
                    <img className="image" src={article.image} />

                    <div className="title">{article.title}</div>
                    <div className="date">{article.date}</div>
                    <div className="text">{article.text}</div>
                </div>

                <Footer />
            </div>
        )
    }
}

export default withRouter(Article);