import React from "react";
import { Link, withRouter } from "react-router-dom";

import { isLogged, evaluateRegister, setStorageItem, getStorageItem, samples, API } from "../config/config";
import Api from "../config/Api";

import Header from "../components/Header";
import Footer from "../components/Footer";
import Popup from "../components/Popup";

import "../styles/register.css";

class Register extends React.Component {

    state = {
        offset: 0,

        user: null,

        name: "",
        email: "",
        password: "",
        repeatPassword: "",
        phone: "",
        address: "",
        psc: "",
        city: "",
        country: "",

        passwordCode: "",

        popup: false,
        loading: true,
        message: "",

        problem: 1,
        sample: 1,
        highlightedSample: 1,
        samples: [],

        knowDoterra: false,
        agree1: false,
        agree2: false,
        wantRegister: true
    }

    constructor() {
        super();

        this.preRegister = this.preRegister.bind(this);
        this.billingRegister = this.billingRegister.bind(this);
        this.passwordRegister = this.passwordRegister.bind(this);
        this.loadSamples = this.loadSamples.bind(this);
        this.handleKeyPress = this.handleKeyPress.bind(this);
    }

    async preRegister() {
        this.setState({ popup: true, loading: true })

        const { email, knowDoterra, sample } = this.state;

        const register = await Api.preRegister({
            email: email,
            knowDoterra: knowDoterra,
            sample: 1
        });

        console.log(register);

        if (register.message === "User pre-registered successfully") {
            this.setState({ popup: false })
            this.props.history.push("/registracia-fakturacne-udaje");
        } else {
            this.setState({ loading: false, message: register.message })
        }
    }

    async billingRegister() {
        this.setState({ popup: true, loading: true })

        const { email, name, address, psc, city, country, phone } = this.state;
        const { agree1, agree2 } = this.state;

        if (agree1 && agree2) {
            const register = await Api.billingRegister({
                email: email,
                name: name,
                address: address,
                psc: psc,
                city: city,
                country: country,
                phone: phone
            });
    
            console.log(register);
    
            if (register.message === "Billing details added succesfully") {
                if (this.state.wantRegister) {
                    const sendCode = await Api.codeRegister({
                        email: email
                    })
    
                    console.log(sendCode)
    
                    if (sendCode.message === "Register code sent successfully") {
                        this.setState({ popup: false })
                        this.props.history.push("/registracia-vytvorit-ucet");
                    } else {
                        this.setState({ loading: false, message: sendCode.message })
                    }
                } else {
                    this.setState({ popup: false })
                    this.props.history.push("/registracia-suhrn-objednavky");
                }
            } else {
                this.setState({ loading: false, message: register.message })
            }
        } else {
            this.setState({ loading: false, message: "Musíte súhlasiť s obchodnými podmienkami a so spracovaním osobných údajov na vytvorenie objednávky" })
        }
    }

    async passwordRegister() {
        this.setState({ popup: true, loading: true })

        const { email, passwordCode, password, repeatPassword } = this.state;

        if (password === repeatPassword) {
            const register = await Api.passwordRegister({
                email: email,
                code: passwordCode,
                password: password
            });

            console.log(register)

            if (register.message === "User registered successfully") {
                this.setState({ popup: false })
                this.props.history.push("/registracia-suhrn-objednavky")
            } else {
                this.setState({ loading: false, message: register.message })
            }
        } else {
            this.setState({ loading: false, message: "Heslá sa nezhodujú" })
        }
    }

    loadSamples(problem) {
        var newSamples = []

        for (let i = 0; i < samples.length; i++) {
            if (samples[i].problems.includes(problem)) {
                newSamples.push(samples[i])
            }
        }

        this.setState({
            samples: newSamples,
            problem: problem,
            highlightedSample: 1,
            sample: newSamples[0]
        })
    }

    handleKeyPress(event) {
        
    }

    async componentDidMount() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        window.addEventListener('resize', this.updateOffset.bind(this));

        /*if (isLogged()) {
            this.props.history.push("/profil")
        }*/

        if (this.props.stage === 1) {
            this.loadSamples(1);
        }
    }

    async componentDidUpdate(prevProps) {
        if (this.props.stage !== prevProps.stage) {
            
        }
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateOffset.bind(this));
    }

    updateOffset() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        this.forceUpdate();
    }

    render() {
        if (this.props.stage === 1) {
            return(
                <div className="screen" id="register">
                    <Header />
    
                    {this.state.popup ? (
                        <Popup
                            type="info"
                            title={this.state.message}
                            loading={this.state.loading}
                            onClick={() => this.setState({ popup: false })}
                        />
                    ) : null}
    
                    <div className="content" style={{ paddingTop: this.state.offset }}>
                        <div className="left-panel">
                            <img className="icon" src={require("../assets/family.png")} />
                        </div>
    
                        <div className="right-panel">
                            <div className="title">Získaj vzorky zadarmo</div>
                            <div className="text">
                                Po zaregistrovaní sa do tímu TerraMia budete môcť nakupovať omnoho výhodnejšie, ako keby ste nakupovali anonýmne. Dokonca dostanete prvú skúšobnú vzorku esenciálnych olejov úplne zadarmo!
                            </div>

                            <input className="field" onKeyPress={this.handleKeyPress} type="text" required value={this.state.email} placeholder="E-mail" onChange={(event) => this.setState({ email: event.target.value})} />

                            <br />
                            <br />

                            <div className="heading">Poznáte doTERRA esenciálne oleje?</div>
                            <div className="choice">
                                <div className="item" style={this.state.knowDoterra ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.setState({ knowDoterra: true })}>Áno</div>
                                <div className="item" style={!this.state.knowDoterra ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.setState({ knowDoterra: false })}>Nie</div>
                            </div>

                            <br />
                            <br />
                            <br />

                            <div className="heading">Ktorý zdravotný problém vás trápi?</div>
                            <div className="problem-list">
                                <div className="item" style={this.state.problem === 1 ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.loadSamples(1)}>Tráviaci systém</div>
                                <div className="item" style={this.state.problem === 2 ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.loadSamples(2)}>Dýchací systém</div>
                                <div className="item" style={this.state.problem === 3 ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.loadSamples(3)}>Pohybový systém</div>
                                <div className="item" style={this.state.problem === 4 ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.loadSamples(4)}>Srdcovo cievny systém</div>
                                <div className="item" style={this.state.problem === 5 ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.loadSamples(5)}>Vylučovací systém</div>
                                <div className="item" style={this.state.problem === 6 ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.loadSamples(6)}>Nervový systém</div>
                                <div className="item" style={this.state.problem === 7 ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.loadSamples(7)}>Lymfatický systém</div>
                                <div className="item" style={this.state.problem === 8 ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.loadSamples(8)}>Imunitný systém</div>
                                <div className="item" style={this.state.problem === 9 ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.loadSamples(9)}>Hormonálny systém</div>
                                <div className="item" style={this.state.problem === 10 ? { backgroundColor: "#A161B3", color: "white" } : null} onClick={() => this.loadSamples(10)}>Pokožka</div>
                            </div>

                            <div className="samples">
                                {this.state.samples.map((sample, index) => (
                                    <div className="item" onClick={() => this.setState({ sample: sample.type, highlightedSample: (index + 1) })} style={this.state.highlightedSample === (index + 1) ? { backgroundColor: "rgba(0, 0, 0, 0.07)" } : null}>
                                        <img className="image" src={sample.image} />

                                        <div className="info">
                                            <div className="name">{sample.name}</div>
                                            <div className="nickname">{sample.nickname}</div>
                                            <div className="text">{sample.text}</div>
                                        </div>
                                    </div>
                                ))}
                            </div>

                            <div className="button-filled" onClick={() => this.preRegister()}>Pokračovať</div>
                        </div>
                    </div>
    
                    <Footer />
                </div>
            )
        }

        if (this.props.stage === 2) {
            return(
                <div className="screen" id="register">
                    <Header />
    
                    {this.state.popup ? (
                        <Popup
                            type="info"
                            title={this.state.message}
                            loading={this.state.loading}
                            onClick={() => this.setState({ popup: false })}
                        />
                    ) : null}
    
                    <div className="content" style={{ paddingTop: this.state.offset }}>
                        <div className="left-panel">
                            <img className="icon" src={require("../assets/family.png")} />
                        </div>
    
                        <div className="right-panel">
                            <div className="title">Kam Vám ma objednávka prísť?</div>
                            <div className="text">
                                Po zaregistrovaní sa do tímu TerraMia budete môcť nakupovať omnoho výhodnejšie, ako keby ste nakupovali anonýmne. Dokonca dostanete prvú skúšobnú vzorku esenciálnych olejov úplne zadarmo!
                            </div>

                            <input className="field" onKeyPress={this.handleKeyPress} type="text" required value={this.state.name} placeholder="Meno a priezvisko" onChange={(event) => this.setState({ name: event.target.value})} />
                            <br />
                            <input className="field" style={{ marginBottom: 10 }} onKeyPress={this.handleKeyPress} type="text" required value={this.state.phone} placeholder="Telefónne číslo" onChange={(event) => this.setState({ phone: event.target.value})} />
                            <br />
                            <input className="field" onKeyPress={this.handleKeyPress} type="text" required value={this.state.address} placeholder="Adresa a číslo domu" onChange={(event) => this.setState({ address: event.target.value})} />
                            <input className="field" onKeyPress={this.handleKeyPress} type="text" required value={this.state.psc} placeholder="PSČ" onChange={(event) => this.setState({ psc: event.target.value})} />
                            <input className="field" onKeyPress={this.handleKeyPress} type="text" required value={this.state.city} placeholder="Mesto" onChange={(event) => this.setState({ city: event.target.value})} />
                            <input className="field" onKeyPress={this.handleKeyPress} type="text" required value={this.state.country} placeholder="Krajina" onChange={(event) => this.setState({ country: event.target.value})} />

                            <br />

                            <div className="checkbox" onClick={() => this.setState((state) => ({ wantRegister: !state.wantRegister }))}>
                                <div className="bullet" style={this.state.wantRegister ? { backgroundColor: "#A161B3" } : null}></div>
                                <div className="title">Vytvoriť účet TerraMia</div>
                            </div>

                            <br />

                            <div className="checkbox" onClick={() => this.setState((state) => ({ agree1: !state.agree1 }))}>
                                <div className="bullet" style={this.state.agree1 ? { backgroundColor: "#A161B3" } : null}></div>
                                <div className="title">Súhlasím s <b>obchodnými podmienkami</b></div>
                            </div>

                            <div className="checkbox" onClick={() => this.setState((state) => ({ agree2: !state.agree2 }))}>
                                <div className="bullet" style={this.state.agree2 ? { backgroundColor: "#A161B3" } : null}></div>
                                <div className="title">Súhlasím so spracovaním osobných údajov</div>
                            </div>

                            <div className="button-filled" onClick={() => this.billingRegister()}>Pokračovať</div>
                        </div>
                    </div>
    
                    <Footer />
                </div>
            )
        }

        if (this.props.stage === 3) {
            return(
                <div className="screen" id="register">
                    <Header />
    
                    {this.state.popup ? (
                        <Popup
                            type="info"
                            title={this.state.message}
                            loading={this.state.loading}
                            onClick={() => this.setState({ popup: false })}
                        />
                    ) : null}
    
                    <div className="content" style={{ paddingTop: this.state.offset }}>
                        <div className="left-panel">
                            <img className="icon" src={require("../assets/family.png")} />
                        </div>
    
                        <div className="right-panel">
                            <div className="title">Stante sa členom TerraMia</div>
                            <div className="text">
                                Po zaregistrovaní sa do tímu TerraMia budete môcť nakupovať omnoho výhodnejšie, ako keby ste nakupovali anonýmne. Dokonca dostanete prvú skúšobnú vzorku esenciálnych olejov úplne zadarmo!
                            </div>

                            <input className="field" onKeyPress={this.handleKeyPress} type="text" required value={this.state.passwordCode} placeholder="Kód na overenie identity" onChange={(event) => this.setState({ passwordCode: event.target.value})} />
                            <br />
                            <input className="field" onKeyPress={this.handleKeyPress} type="password" required value={this.state.password} placeholder="Heslo" onChange={(event) => this.setState({ password: event.target.value})} />
                            <input className="field" onKeyPress={this.handleKeyPress} type="password" required value={this.state.repeatPassword} placeholder="Zopakovať heslo" onChange={(event) => this.setState({ repeatPassword: event.target.value})} />

                            <div className="button-filled" onClick={() => this.passwordRegister()}>Vytvoriť účet</div>
                        </div>
                    </div>
    
                    <Footer />
                </div>
            )
        }

        if (this.props.stage === 4) {
            const user = {
                email: this.state.email,
                name: this.state.name,
                phone: this.state.phone,
                address: this.state.address,
                psc: this.state.psc,
                city: this.state.city,
                country: this.state.country
            }

            return(
                <div className="screen" id="register">
                    <Header />
    
                    {this.state.popup ? (
                        <Popup
                            type="info"
                            title={this.state.message}
                            loading={this.state.loading}
                            onClick={() => this.setState({ popup: false })}
                        />
                    ) : null}
    
                    <div className="content" style={{ paddingTop: this.state.offset }}>
                        <div className="left-panel">
                            <img className="icon" src={require("../assets/family.png")} />
                        </div>
    
                        <div className="right-panel">
                            <div className="title">Ďakujeme Vám za Vašu objednávku</div>
                            <div className="text">
                                Po zaregistrovaní sa do tímu TerraMia budete môcť nakupovať omnoho výhodnejšie, ako keby ste nakupovali anonýmne. Dokonca dostanete prvú skúšobnú vzorku esenciálnych olejov úplne zadarmo!
                            </div>

                            <div className="heading">Súhrn objednávky</div>

                            <div className="details">
                                <div className="heading">Meno a priezvisko</div>
                                <div className="info">{user.name}</div>
                                <div className="heading">E-mail</div>
                                <div className="info">{user.email}</div>
                                <div className="heading">Telefónne číslo</div>
                                <div className="info">{user.phone}</div>
                                <div className="heading">Adresa</div>
                                <div className="info">{user.address}</div>
                                <div className="heading">PSČ</div>
                                <div className="info">{user.psc}</div>
                                <div className="heading">Mesto</div>
                                <div className="info">{user.city}</div>
                                <div className="heading">Krajina</div>
                                <div className="info">{user.country}</div>
                            </div>

                            <div className="button-filled" onClick={() => this.props.history.push("/prihlasenie")}>Prihlásiť sa</div>
                        </div>
                    </div>
    
                    <Footer />
                </div>
            )
        }
    }
}

export default withRouter(Register);