import React from "react";

import { getStorageItem, API_URL } from "../config/config";

import Loading from "../components/Loading";

import Api from "../config/Api";

import "../styles/order.css";

export default class Order extends React.Component {

    state = {
        client: {},
        products: [],
        value: 0,
        date: "",

        loading: true
    }

    constructor() {
        super();

        this.loadData = this.loadData.bind(this);
        this.formatDate = this.formatDate.bind(this);
    }

    async loadData() {
        const token = getStorageItem("token");
        const order = this.props.order;

        // LOAD PRODUCTS
        var ids = this.filter(order.products);
        var products = [];

        for (let i = 0; i < ids.length; i++) {
            const id = ids[i]
            
            const product = await Api.getProduct(id.id);
            
            products.push({
                ...product.product,
                amount: id.amount
            });
        }

        this.setState({ products: products })

        // LOAD CLIENT
        const clientId = order.orderedBy;
        const client = await Api.getClient(clientId, token);

        this.setState({ client: client.user });

        // LOAD DATE
        this.setState({ date: this.formatDate(order.date) });

        this.setState({ loading: false })
    }

    filter(ids) {
        var products = [];

        for (let i = 0; i < ids.length; i++) {
            const id = ids[i];

            if (products.length === 0) {
                products.push({
                    id: id,
                    amount: 1
                });

                continue;
            }

            for (let j = 0; j < products.length; j++) {
                if (products[j].id === id) {
                    products[j].amount = products[j].amount + 1;
                } else {
                    if (j === products.length - 1) {
                        products.push({
                            id: id,
                            amount: 1
                        });

                        break;
                    }
                }
            }
        }

        return products;
    }

    formatDate(text) {
        const dateString = text.split("T")[0].split("-");
        const timeString = text.split("T")[1].split(".")[0].split(":");

        const date = dateString[2] + "." + dateString[1] + "." + dateString[0];
        const time = (parseInt(timeString[0]) + 1) + ":" + timeString[1];

        return date + ", " + time;
    }

    componentDidMount() {
        this.loadData();
    }

    render() {
        if (this.state.loading) {
            return (
                <div className="order">
                    <Loading />
                </div>
            )
        }

        return(
            <div className="order">
                <div className="name">{this.state.client.name}</div>
                <div className="date">{this.state.date}</div>

                <div className="data">
                    <div className="user-info">
                        <div className="heading">E-mail</div>
                        <div className="info">{this.state.client.email}</div>

                        <div className="heading">Telefónne číslo</div>
                        <div className="info">{this.state.client.phone}</div>

                        <div className="heading">Adresa</div>
                        <div className="info">{this.state.client.address}</div>

                        <div className="heading">PSČ</div>
                        <div className="info">{this.state.client.psc}</div>

                        <div className="heading">Mesto</div>
                        <div className="info">{this.state.client.city}</div>

                        <div className="heading">Krajina</div>
                        <div className="info">{this.state.client.country}</div>

                        <div className="heading" style={{ marginTop: 30 }}>Celková suma</div>
                        <div className="info" style={{ marginTop: 30 }}>{(this.props.order.value / 100).toFixed(2)}€</div>
                    </div>

                    <div className="products">
                        <div className="slider">
                            {this.state.products.map((product) => <Product product={product} />)}
                        </div>
                    </div>
                </div>

                {this.props.order.status === "paid" ? <div className="button-filled" onClick={() => this.props.fulfill(this.props.order._id)}>Vybavená</div> : null}
            </div>
        )
    }
}

function Product(props) {
    const product = props.product;
    const src = API_URL + "/uploads/" + product.imagePath

    return(
        <div className="product">
            <img className="image" src={src} />

            <div className="name">{product.name}</div>
            <div className="amount">{product.amount}x</div>
        </div>
    )
}